.checkbox_style {
  font-size: 13px;
  font-weight: 400;
}
.checkbox_style [type="checkbox"]:checked,
.checkbox_style [type="checkbox"]:not(:checked) {
  position: absolute;
  left: -9999px;
}
.checkbox_style [type="checkbox"]:checked + label,
.checkbox_style [type="checkbox"]:not(:checked) + label {
  position: relative;
  padding-left: 24px;
  cursor: pointer;
  line-height: 20px;
  display: inline-block;
  margin-bottom: 0;
}
.checkbox_style [type="checkbox"]:checked + label:before,
.checkbox_style [type="checkbox"]:not(:checked) + label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 4px;
  width: 14px;
  height: 14px;
  border: 1px solid #dbd9e6;
  background: transparent;
}
.checkbox_style [type="checkbox"]:checked + label:before {
  border-color: transparent;
}
.checkbox_style [type="checkbox"]:checked + label:after,
.checkbox_style [type="checkbox"]:not(:checked) + label:after {
  content: "\e64d";
  position: absolute;
  top: 2px;
  left: 0;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  font-size: 16px;
  color: #1e1859;
  font-family: "themify";
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
}
.checkbox_style [type="checkbox"]:not(:checked) + label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0);
}
.checkbox_style [type="checkbox"]:checked + label:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}
